import React, {useEffect, useState} from "react";
import {MySkillsBlock} from "./MySkillsBlock";
import s from './MySkills.module.css'
import c from '../../App.module.css'
import react from '../Images/react.png'
import redux from '../Images/redux.png'
import js from '../Images/js.png'
import ts from '../Images/ts.png'
import html from '../Images/html.png'
import css from '../Images/css.png'


export type ArrayBlockType = {
	title:string
	image:string
	text:string
}

export const images = {
	REACT:react,
	REDUX:redux,
	JS:js,
	TS:ts,
	HTML:html,
	CSS:css,
}

export const MySkills = () => {
	const [myInfoForBlock, setMyInfoForBlock] = useState<Array<ArrayBlockType>>([
		{title: 'React', image:images.REACT,text:'My knowledge and experience'},
		{title: 'Js', image:images.JS,text:'My knowledge and experience'},
		{title: 'Redux', image:images.REDUX,text:'My knowledge and experience'},
		{title: 'TS', image:images.TS,text:'My knowledge and experience'},
		{title: 'HTML', image:images.HTML,text:'My knowledge and experience'},
		{title: 'CSS', image:images.CSS,text:'My knowledge and experience'},
	])
	useEffect(() => {

	},[])


	return (
		<div className={s.mySkillsWrapper}>
			<div className={c.container}>
				<div className={s.mySkillsContainer}>
					<h2>My skills</h2>
				</div>
				<div className={s.wrapperForBlock}>
					{myInfoForBlock.map((el,i) =>
						<MySkillsBlock text={el.text} title={el.title} image={el.image} key={i}/>
					)}
				</div>
			</div>
		</div>
	)
}